

.room-container {
     
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    display: flex;
    width: 100%;
    
    height: 39vh;
    background-color: #ACACAC;
    padding: 0;
    
}

.room-container:nth-of-type(3) {
    margin-top: 3vh;
}

.room-container-roomnum-container {
    /* border: 1px solid yellow; */
    width: 4vw;

}
.room-container-roomnum {
    color: rgb(85, 85, 85);
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 3vw;
    transform-origin: top left;
    transform: rotate(-90deg) translateX(-100%);
    position: relative;
    top: 0;
    left: 0;
    height: 4vw;
    width: 39vh;
    

}

.room1 {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: #F2AC63;
    
}
.room2 {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: #fcd0a2 ;
}

.room-container-notes {
    
    width:13vw;
    color: rgb(103, 102, 102);
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2.5vh;

}
.room-rows {
    display: flex;
    align-items: flex-end;
    width: 80vw;
    
}


.time {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 2.3vh;
    
    width: 13vw;
}

.time::before {
    content: "";
    display: flex;
    border-radius: 50%;
    background-color: white;
    height: 2vh;
    width: 2vh;
    margin-right: .7vw;
    margin-left: .48vw;
   
    
}


.shiftblock-container-ampm {
    border-right: .2vw solid #f5b9799c;
    border-left: .2vw solid #f5b9799c;
    width: 14vw;
    margin-left: 1vw;
    height: 100%;

}



.pmshiftblock {
    margin-top: 2vh;
}

.amshiftblock {
    margin-top: 1vh;
}

h2 {

    font-size: 2.5vh;
    color: white;
}